@charset "utf-8";
* {
  margin: 0;
  padding: 0;
}
/*主体高度*/
/*模块主题颜色*/
/*文字*/
body {
  font-family: 'MicrosoftYaHei';
  font-size: 24px;
  text-align: center;
  color: #fff;
  min-width: 900px;
}
#header {
  width: 100%;
  height: 80px;
  line-height: 80px;
  background: #9370DB;
}
#aside {
  width: 300px;
  float: left;
  min-height: 500px;
  background: #9ACD32;
}
#aside ul li {
  width: 100%;
  height: 30px;
  line-height: 30px;
  font-size: 18px;
  background: orange;
  border-bottom: 2px solid #fff;
  cursor: pointer;
}
#aside ul li:hover {
  background: #cc3300;
}
#aside ul li a {
  width: 100%;
  height: 100%;
  text-decoration: none;
  display: block;
  color: #fff;
}
#aside .searchResult {
  width: 100%;
  background: #838B8B;
  position: relative;
  font-size: 18px;
}
#aside .searchResult .title {
  padding: 4px;
  background: #9370DB;
}
#aside .searchResult .title span {
  font-size: 12px;
  display: inline-block;
  padding: 2px 3px;
  border: 1px solid #fff;
  border-radius: 3px;
  float: right;
  position: absolute;
  top: 5px;
  right: 5px;
}
#aside .searchResult .title span:hover {
  cursor: pointer;
}
#aside .searchResult p {
  border-bottom: 1px solid #fff;
}
#main {
  margin-left: 300px;
  min-height: 500px;
  background: #7CFC00;
}
#main .search {
  overflow: hidden;
  width: 80%;
  height: 210px;
  border-radius: 10px;
  box-shadow: 0px 3px 10px 5px rgba(0, 0, 0, 0.5);
  background-color: #ccc;
  position: relative;
  margin: 0 auto;
  top: 90px;
}
#main .search h2 {
  margin: 0;
  padding: 20px 0;
  background: #9370DB;
  font-size: 24px;
}
#main .search .inputWrap {
  width: 70%;
  margin: 0 auto;
  margin-top: 25px;
}
#main .search .inputWrap span {
  font-size: 28px;
  position: relative;
  top: 3px;
}
#main .search .inputWrap input {
  width: 60%;
  height: 35px;
  line-height: 35px;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  text-indent: 5px;
  margin-left: 10px;
  color: #666;
  padding: 5px;
}
#main .search .btn {
  background: #9370DB;
  padding: 5px 30px;
  font-size: 22px;
  margin-top: 20px;
}
#main .search .btn:hover {
  color: #fff;
}
#footer {
  width: 100%;
  height: 80px;
  line-height: 80px;
  background: #838B8B;
}
